<template>
    <el-card class="box-card common_box">
        <h3 class="title">建筑物管理</h3>
        <br />
        <el-button type="primary" @click="showAddBox()">添加数据</el-button>
        <br />
        <br />
        <div>
            <el-table border size="small" stripe :data="list" style="width: 100%">
                <el-table-column prop="uniqueId" label="uniqueId" width="100"></el-table-column>
                <el-table-column prop="appHost" label="小程序连接地址" width="140"></el-table-column>
                <el-table-column prop="blockName" label="建筑物名称" width="180"></el-table-column>
                <el-table-column prop="blockDesc" label="建筑物描述"></el-table-column>
                <el-table-column prop="serverHost" label="服务地址" width="180"></el-table-column>
                <el-table-column label="功能" width="180">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="showFixBox(scope.row)" size="mini">修改</el-button>
                        <el-button type="danger" @click="deleteFunc(scope.row)" size="mini">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <el-dialog :title="title" :visible.sync="visible" width="50%" center>
            <el-form :model="form" label-width="120px">
                <el-form-item label="uniqueId" prop="uniqueId">
                    <el-input v-model="form.uniqueId"></el-input>
                </el-form-item>
                <el-form-item label="小程序连接地址" prop="appHost">
                    <el-input v-model="form.appHost"></el-input>
                </el-form-item>
                <el-form-item label="建筑物名称" prop="blockName">
                    <el-input v-model="form.blockName"></el-input>
                </el-form-item>
                <el-form-item label="建筑物描述" prop="blockDesc">
                    <el-input v-model="form.blockDesc"></el-input>
                </el-form-item>
                <el-form-item label="服务地址" prop="serverHost">
                    <el-input v-model="form.serverHost"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="reset">取 消</el-button>
                <el-button type="primary" @click="fixFunc">确 定</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>

<script>
import { cookie } from "@/util.js";
import ajax from "@/request.js";

export default {
    data() {
        return {
            user: "",
            list: [],
            visible: false,
            title: "添加数据",
            form: {
                appHost: "",
                blockName: "",
                blockDesc: "",
                serverHost: "",
                uniqueId: ""
            }
        };
    },
    inject: ["reload"],
    created() {
        this.user = cookie.get("user");
        if (!this.user) {
            this.$router.replace({
                path: "/login"
            });
        }
        this.queryData();
    },
    methods: {
        queryData() {
            ajax.get("/manager/blockList", {}).then(res => {
                if (res.code == 200 && res.data.length) {
                    this.list = res.data;
                }
            });
        },
        showAddBox() {
            this.visible = true;
            this.title = "添加数据";
        },
        showFixBox(row) {
            this.title = "修改数据";
            this.visible = true;
            this.form = row;
        },
        fixFunc() {
            if (this.title == "添加数据") {
                ajax.post(`/manager/addBlock`, this.form).then(res => {
                    if (res.code == 200) {
                        this.$message({
                            type: "success",
                            message: "添加数据成功"
                        });
                        this.reset();
                        this.queryData();
                    }
                });
            } else {
                ajax.post(`/manager/updateBlock`, this.form).then(res => {
                    if (res.code == 200) {
                        this.$message({
                            type: "success",
                            message: "修改数据成功"
                        });
                        this.reset();
                        this.queryData();
                    }
                });
            }
        },
        reset() {
            this.visible = false;
            this.form = {};
        },
        deleteFunc(row) {
            this.$confirm("你确定要删除吗, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    ajax.post(`/manager/delBlock/${row.uuid}`, {}).then(
                        res => {
                            this.$message({
                                type: "success",
                                message: "删除成功!"
                            });
                            this.queryData();
                        }
                    );
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除"
                    });
                });
        }
    }
};
</script>
<style  scoped>
.common_box {
    min-height: 300px;
}
.common_box .title {
    margin-bottom: 0;
}
</style>
