<template>
    <el-container>
        <el-header class="header">
            <div class="header_wrap">
                <h3 class="title">
                    <strong>
                        <svg-icon icon-class="map" />
                        <strong> 建筑物管理后台</strong>
                    </strong>
                    <span>Building Management System</span>
                </h3>
                <div class="login_info">
                    <i class="el-icon-user-solid"></i>
                    <b>&nbsp;&nbsp;{{ user }}&nbsp;&nbsp;&nbsp;</b>
                    <el-button @click="logout" type="primary">退出登录</el-button>
                </div>
            </div>
        </el-header>
        <div class="nav">
            <el-menu :default-active="$route.path" mode="horizontal" :router="true">
                <el-menu-item index="/dashboard">
                    <i class="el-icon-s-home" />管理首页
                </el-menu-item>
            </el-menu>
        </div>
        <el-main class="main">
            <el-row>
                <el-col :span="24">
                    <router-view v-if="isRouterAlive"></router-view>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
import { cookie } from "@/util.js";
import { AuthURL } from "@/config.js";
import ajax from "@/request";

export default {
    data() {
        return {
            dialogVisible: false,
            user: "",
            isRouterAlive: true
        };
    },
    provide() {
        return {
            reload: this.reload
        };
    },
    created() {
        this.user = cookie.get("user");
    },
    components: {},
    methods: {
        reload() {
            this.isRouterAlive = false;
            this.$nextTick(function() {
                this.isRouterAlive = true;
            });
        },
        logout() {
            cookie.del("token");
            cookie.del("user");

            ajax
                .post(`/admin/logout`, {
                    username: cookie.get("user")
                })
                .then(data => {
                    if (data.code == 200) {
                        this.$router.replace({
                            path: "/login"
                        });
                    }
                });
        }
    }
};
</script>

