<template>
    <div class="container">
        <div class="login-page">
            <h2 class="login_title">
                <span class="normal big">密码登录</span>
            </h2>
            <el-form ref="form" :model="form" label-width="0">
                <el-form-item label prop="name">
                    <el-input v-model="form.username" placeholder="用户名/邮箱/手机号/身份证号">
                        <el-button slot="prepend" icon="el-icon-user-solid"></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item label>
                    <el-input v-model="form.password" type="password" placeholder="请输入密码">
                        <el-button slot="prepend" icon="el-icon-s-release"></el-button>
                    </el-input>
                </el-form-item>
                <el-button
                    class="cross"
                    type="primary"
                    icon="el-icon-user-solid"
                    @click="doLogin"
                >登 录</el-button>
            </el-form>
        </div>
    </div>
</template>
<script>
import { cookie } from "@/util.js";
import { AuthURL } from "@/config.js";
import ajax from "@/request";

export default {
    data() {
        return {
            form: {
                username: "",
                password: ""
            }
        };
    },
    methods: {
        doLogin() {
            if (!this.form.username) {
                this.$message.error("用户名必填");
            } else if (!this.form.password) {
                this.$message.error("密码必填");
            } else {
                ajax
                    .post(
                        `/admin/login`,
                        this.form
                    )
                    .then(data => {
                        if (data.code == 200) {
                            cookie.set("token", data.data.userToken);
                            cookie.set("user", data.data.username);
                            this.$router.push({ path: "/dashboard" });
                        }
                    });
            }
        }
    }
};
</script>
